<template>
	<div id="estore-order-detail">
		<public-header title="订单详情"></public-header>
		<div class="content font-14">
			<div class="order-status">
				<div>
					<span class="font-18 font-bold" @click="onTogglePopup('log')">订单{{order.status_cn}}</span><i class="icon icon-right c-gray"></i>
				</div>
				<div class="c-gray margin-15-tb font-14" v-if="order.log && order.delivery_status != 7 && order.log.note">{{order.log.note}}</div>
				<div class="font-15 margin-15-t margin-10-b" v-if="order.status == 2 && order.order_from == 2">
					<i class="icon icon-timefill font-16"></i>
					<span class="c-gray">配送时间:</span>
					<span class="c-danger">{{order.delivery_time}}</span>
				</div>
				<div class="order-btn margin-10-b margin-15-t" v-if="order.status < 3">
					<template v-if="order.is_pay == 0 && order.status == 1">
						<div class="btn bg-danger" @click="onChangeStatus(order.id, 'pay')">立即支付</div>
					</template>
					<template v-if="order.status == 1">
						<div class="btn bg-danger" @click="onChangeStatus(order.id, 'cancel')">取消订单</div>
					</template>
					<template v-else-if="order.status == 2">
						<div class="btn bg-danger" @click="onChangeStatus(order.id, 'cancel')" v-if="order.delivery_status == 1">取消订单</div>
						<div class="btn bg-danger" @click="onChangeStatus(order.id, 'end')">确认送达</div>
					</template>
				</div>
				<template v-if="order.deliveryer_id > 0">
					<div class="divide">
						<div class="divide-line"></div>
					</div>
					<div class="padding-15-lr padding-10-tb flex-lr">
						<div class="flex ">
							<div class="deliveryer-avatar">
								<img class="img-100" :src="order.deliveryer.avatar" alt="">
							</div>
							<div class="font-14">
								<div class="flex">
									<span class="margin-5-r">{{order.deliveryer.title}}</span>
									<div class="allocation-icon" v-if="0">{{order.delivery_title}}</div>
								</div>
								<div class="c-gray margin-10-t">{{order.deliveryer.mobile}}</div>
							</div>
						</div>
						<div class="flex">
							<div class="icon icon-telephone icon-telephone1" @click="util.jsTel(order.deliveryer.mobile)"></div>
							<div class="icon icon-zuobiao font-16 margin-10-l" v-if="order.show_location == 1" @click="util.jsUrl('/plugin/pages/paotui/store/order/map', {id: order.id})"></div>
						</div>
					</div>
				</template>
			</div>
			<div class="flex-lr padding-15 margin-10 bg-default" @click="util.jsUrl('/plugin/pages/paotui/store/order/refund', {id: order.id})" v-if="order.refund_status > 0">
				<div>退款明细</div>
				<div class="flex">
					<span class="c-gray margin-5-r">查看</span>
					<span class="icon icon-right font-12 c-gray"></span>
				</div>
			</div>
			<div class="bg-default margin-10 padding-15-lr">
				<template v-if="order.order_type == 'take'">
					<div class="address-item from van-hairline--bottom">
						<div class="left">
							<div class="mark">发</div>
						</div>
						<div class="right">
							<div class="title">{{order.buy_address}}</div>
							<div class="desc">
								<span>{{order.buy_username}}</span>
								<span>{{order.buy_sex}}</span>
								<span>{{order.buy_mobile}}</span>
							</div>
						</div>
					</div>
					<div class="address-item to">
						<div class="left">
							<div class="mark">收</div>
						</div>
						<div class="right">
							<div class="title">{{order.accept_username}}</div>
							<div class="desc">{{order.accept_address}}</div>
							<div class="title margin-5-t">{{order.accept_mobile}}</div>
						</div>
					</div>
				</template>
				<template v-else-if="order.order_type == 'send'">
					<div class="address-item from van-hairline--bottom">
						<div class="left">
							<div class="mark">发</div>
						</div>
						<div class="right">
							<div class="title">{{order.buy_username}}</div>
							<div class="desc">{{order.buy_address}}</div>
							<div class="title margin-5-t">{{order.buy_mobile}}</div>
						</div>
					</div>
					<div class="address-item to">
						<div class="left">
							<div class="mark">收</div>
						</div>
						<div class="right">
							<div class="title">{{order.accept_address}}</div>
							<div class="desc">
								<span>{{order.accept_username}}</span>
								<span>{{order.accept_sex}}</span>
								<span>{{order.accept_mobile}}</span>
							</div>
						</div>
					</div>
				</template>
			</div>
			<div class="bg-default margin-10 padding-15 font-14">
				<van-row class="padding-10-b">
					<van-col span="7" class="c-gray">订单编号</van-col>
					<van-col span="14">{{order.order_sn}}</van-col>
					<van-col span="3" class="text-right">
						<span class="c-danger" v-clipboard:copy="order.order_sn" v-clipboard:success="onCopy">复制</span>
					</van-col>
				</van-row>
				<van-row class="padding-10-tb">
					<van-col span="7" class="c-gray">订单类型</van-col>
					<van-col span="17">{{order.order_type == 'take' ? '帮取' : '帮送'}}</van-col>
				</van-row>
				<van-row class="padding-10-tb">
					<van-col span="7" class="c-gray">物品类型</van-col>
					<van-col span="17">
						<span>{{order.data.goods_type}}</span>
						<span>/</span>
						<span>{{order.goods_weight}}公斤</span>
					</van-col>
				</van-row>
				<van-row class="padding-10-tb">
					<van-col span="7" class="c-gray">配送距离</van-col>
					<van-col span="17">
						<span>{{order.distance}}公里</span>
					</van-col>
				</van-row>
				<van-row class="padding-10-tb">
					<van-col span="7" class="c-gray">物品来源</van-col>
					<van-col span="17">
						<span>{{order.data.goods_source}}</span>
						<span>#{{order.data.serial_sn}}</span>
					</van-col>
				</van-row>
				<van-row class="padding-10-tb">
					<van-col span="7" class="c-gray">配送时间</van-col>
					<van-col span="17">
						{{order.delivery_time}}
					</van-col>
				</van-row>
				<van-row class="padding-10-tb">
					<van-col span="7" class="c-gray">备注</van-col>
					<van-col span="17">
						{{order.note}}
					</van-col>
				</van-row>
				<van-row class="padding-10-tb">
					<van-col span="7" class="c-gray">支付方式</van-col>
					<van-col span="17">
						预存金支付
					</van-col>
				</van-row>
				<van-row class="padding-10-t">
					<van-col span="7" class="c-gray">支付金额</van-col>
					<van-col span="12">
						{{order.final_fee}}{{Lang.dollarSignCn}}
					</van-col>
					<van-col span="5" class="text-right">
						<span class="c-danger" @click="onTogglePopup('pricedetail')">价格明细</span>
					</van-col>
				</van-row>
			</div>
		</div>

		<!--订单日志-->
		<van-popup v-model="popup.log" position="bottom">
			<div class="popup-title van-hairline--bottom">
				<span class="opacity-1">取消</span>
				<span>订单跟踪</span>
				<span class="c-danger font-14" @click="onTogglePopup('log')">关闭</span>
			</div>
			<div class="popup-content">
				<van-steps direction="vertical" :active="order.logs_length - 1" active-color="#44BB00" active-icon="roundcheckfill">
					<van-step class="border-0px" v-for="(item, index) in order.logs" :key="index">
						<div class="flex-lr">
							<span>{{item.title}}</span>
							<span>{{item.addtime_cn}}</span>
						</div>
						<div class="margin-5-t">{{item.note}}</div>
					</van-step>
				</van-steps>
			</div>
		</van-popup>
		<!--价格明细-->
		<van-popup v-model="popup.pricedetail" position="bottom">
			<div class="popup-title van-hairline--bottom">
				<span class="c-gray font-14" @click="onTogglePopup('pricedetail')">关闭</span>
				<span>价格明细</span>
				<span class="c-danger font-14 flex" @click="util.jsUrl('/plugin/pages/paotui/store/order/feeRule')">
					价格规则
					<span class="icon icon-right font-12 "></span>
				</span>
			</div>
			<div class="popup-content">
				<van-cell-group class="border-0px">
					<template v-for="(item, index) in order.data.fees">
						<van-cell
							class="border-0px"
							:title="item.title"
							:value="item.fee_cn"
							v-if="item.fee != ''"
						/>
					</template>
				</van-cell-group>
			</div>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import PublicFooter from '@/components/footer'
	export default {
		components: {
			PublicHeader,
			PublicFooter
		},
		data() {
			return {
				order: {
					status: 0,
					data: {
						order: {},
						fees: []
					}
				},
				config: {},
				popup: {
					log: false,
					pricedetail: false
				},
				showPreLoading: true
			}
		},
		methods: {
			onLoad() {
				if(!this.id) {
					this.$toast('订单不存在或已删除');
					return false;
				}
				this.util.request({
					url: 'errander/storeOrder/detail',
					data: {
						id: this.id,
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return false;
					}
					result = result.message;
					this.order = this.util.extend(this.order, result.order);
				})
			},

			onChangeStatus(oid, type) {
				if(type == 'cancel') {
					var params = {
						url: 'errander/storeOrder/cancel?id=' + oid,
						confirm: '确定取消订单吗?',
					}
				} else if(type == 'end') {
					var params = {
						url: 'errander/storeOrder/end?id=' + oid,
						confirm: '确定已收到商品吗?',
					}
				} else if(type == 'pay') {
					var params = {
						url: 'errander/storeOrder/pay?id=' + oid,
						confirm: '确定去支付吗?',
					}
				}
				this.util.jspost(params);
			},

			onTogglePopup(key) {
				this.popup[key] = !this.popup[key];
			},

			onCopy(e) {
				this.util.$toast('复制成功');
			},
		},
		created() {
			this.query = this.$route.query;
			this.id = this.query.id;
		},
		mounted() {
			this.onLoad()
		}
	}
</script>

<style>
	@import "../../../../../static/css/test.css";
	#estore-order-detail .order-status{
		margin: 10px;
		padding: 15px 0 5px 0;
		text-align: center;
		background-color: #fff;
	}
	#estore-order-detail .order-status .order-btn{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
	}
	#estore-order-detail .order-status .order-btn .btn{
		font-size: 14px;
		padding: 8px 10px;
		border: 1px solid #ddd;
		border-radius:3px;
		margin: 0 5px;
	}
	#estore-order-detail .order-status .allocation-icon{
		display:inline-block;
		position:relative;
		background-color:#33aafc;
		height:15px;
		line-height:15px;
		padding:0 6px;
		font-size:10px;
		color:#fff;
		font-weight:normal;
		vertical-align:middle;
		border-radius:6px 0;
	}
	#estore-order-detail .order-status .icon-zuobiao{
		width: 40px;
		height: 40px;
		line-height: 40px;
		border: 1px solid #e7e7e7;
		border-radius: 100%;
		background-color: #fff;
		color: #656565;
		text-align: center;
	}
	#estore-order-detail .order-status .deliveryer-avatar{
		width: 50px;
		height: 50px;
		border-radius: 50%;
		overflow: hidden;
		margin-right: 10px;
	}
	#estore-order-detail .order-status .icon-telephone1 {
		width: 40px;
		height: 40px;
		line-height: 40px;
		border: 1px solid #e7e7e7;
		border-radius: 100%;
		background-color: #fff;
		color: #656565;
		text-align: center;
	}
	#estore-order-detail .address-item{
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		padding: 15px 0;
	}
	#estore-order-detail .address-item .left{
		width: 10%;
	}
	#estore-order-detail .address-item .right{
		width: 90%;
	}
	#estore-order-detail .address-item .mark{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 20px;
		height: 20px;
		border-radius: 50%;
		font-size: 12px;
		color: #fff;
	}
	#estore-order-detail .address-item.from .mark{
		background-color: #ff2d4b;
	}
	#estore-order-detail .address-item.to .mark{
		background-color: #ECAC5C;
	}
	#estore-order-detail .address-item .title{
		line-height: 1.3;
	}
	#estore-order-detail .address-item .desc{
		line-height: 1.3;
		font-size: 13px;
		color: #999;
		margin-top: 5px;
	}
	#estore-order-detail .van-popup .popup-title{
		height: 44px;
		padding: 0 15px;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
	}
	#estore-order-detail .van-popup .popup-content{
		min-height: 200px;
		max-height: 400px;
		overflow-x: hidden;
		overflow-y: scroll;
	}
</style>



